import React from 'react'
import axios from 'axios'
import API from '../api'
import { addServers, toggleLoader } from '../actions'
import { connect } from 'react-redux'
import './LoginForm.scss'

class LoginForm extends React.Component {
	state = {
		username: '',
		password: '',
		authToken: null,
		badCredentials: false,
		serverError: false,
	}
	
	changeUsername = event => {
		this.setState({ username: event.target.value })
		this.resetErrors()
	}
	changePassword = event => {
		this.setState({ password: event.target.value })
		this.resetErrors()
	}
	handleSubmit = event => {
		event.preventDefault()
		this.props.toggleLoader();
		const payload = {
			username: this.state.username,
			password: this.state.password
		}
		
		axios.post(API.login, { ...payload }).then(response => {
			this.setState({
				authToken: response.data.token
			})
			this.fetchServers()
		}).catch(error => {
			let object = error.response.status === 401 ?
				{ badCredentials: true } :
				{ serverError: true }
			this.setState(object);
			this.props.toggleLoader();
		})
	}
	fetchServers = () => {
		if (!this.state.authToken) return
		let config = {
			headers: {
				'Authorization': this.state.authToken
			}
		}
		axios.get(API.servers, config)
		     .then(response => this.props.addServers(response.data))
		     .catch(() => this.setState({ serverError: true }))
		     .finally(() => this.props.toggleLoader())
	}
	resetErrors = () => {
		this.setState({
			badCredentials: false,
			serverError: false
		})
	}
	componentWillUnmount() {
		this.setState = () => { return }
	}
	
	render () {
		return (
			<div>
				<form className={this.state.badCredentials ? 'bad-input' : ''} onSubmit={this.handleSubmit}>
					<label htmlFor="name">Name</label>
					<input type="text" name="name" value={this.state.username} onChange={this.changeUsername}/>
					<label htmlFor="password">Password</label>
					<input type="password" name="password" value={this.state.password} onChange={this.changePassword}/>
					{this.state.badCredentials && <p>Login failed: Invalid username or password.</p>}
					{this.state.serverError && <p>Server error, please try again later.</p>}
					<button disabled={this.state.username.length === 0 || this.state.password.length === 0} type="submit">Login</button>
				</form>
			</div>
		)
	}
}

function mapDispatchToProps (dispatch) {
	return {
		addServers: servers => dispatch(addServers(servers)),
		toggleLoader: servers => dispatch(toggleLoader(servers))
	}
}

const mapStateToProps = state => {
	return { servers: state.servers }
}

export default connect(mapStateToProps, mapDispatchToProps)(LoginForm)