export const ADD_SERVERS = 'ADD_SERVERS'
export const TOGGLE_LOADER = 'TOGGLE_LOADER'
export const SORT_SERVERS = 'TOGGLE_LOADER'

export function addServers(servers) {
	return { type: ADD_SERVERS, payload: servers }
}
export function toggleLoader() {
	return { type: TOGGLE_LOADER }
}
export function sortServers(column) {
	return { type: SORT_SERVERS, column }
}