import React from 'react';
import { connect } from 'react-redux'
import './Servers.scss'
import { ReactComponent as Logo } from './sorting.svg';
import { addServers } from '../actions'

class Servers extends React.Component {
	state = {
		sortTypeForName: false,
		sortTypeForDistance: false
	}
	sortServers(column) {
		let sortType;
		if(column === 'name') {
			sortType = this.state.sortTypeForName;
			this.setState({ sortTypeForName: !this.state.sortTypeForName });
		} else {
			sortType = this.state.sortTypeForDistance;
			this.setState({ sortTypeForDistance: !this.state.sortTypeForDistance });
		}
		const compare = (a, b) => {
			return a[column] > b[column] ?
		       (sortType ? -1 : 1) :
		       a[column] < b[column] ?
		            (sortType ? 1 : -1) :
		            0
		}
		this.props.addServers(this.props.servers.sort(compare));
	}
	render() {
		return (
			<table className="servers-table">
				<tr className="server-column-labels">
					<th>Name <Logo onClick={() => this.sortServers('name')}/></th>
					<th>Distance <Logo onClick={() => this.sortServers('distance')}/></th>
				</tr>
				{
					this.props.servers.map((object, index) => {
						return <tr className="server" key={index}>
							<td>{object.name}</td>
							<td>{object.distance}</td>
						</tr>
					})
				}
			</table>
		)
	}
}

function mapDispatchToProps (dispatch) {
	return {
		addServers: column => dispatch(addServers(column))
	}
}

const mapStateToProps = state => {
	return { servers: state.servers }
}

export default connect(mapStateToProps, mapDispatchToProps)(Servers)